<template>
  <div class="all-html">
    
    <Loading :active.sync="isLoading"></Loading>
  <div class="jumbotron jumbotron-padding jumbotron-fluid bg-cart-image d-flex align-items-end"></div>

  <div class="my-5 row justify-content-center" v-if="cart.final_total">
    <div class="container">
    <div class="row mb-4 text-center">
    <div class="col">
       <div class="alert alert-primary" role="alert">
          1. 
       </div>
    </div>
    <div class="col">
        <div class="alert alert-danger" role="alert">
          2. 
       </div>
    </div>
    <div class="col">
        <div class="alert alert-secondary" role="alert">
          3. 
       </div>
    </div>
    </div>
    <table class="table">
      <thead>
        <th></th>
        <th></th>
        <th></th>
        <th></th>
      </thead>
      <tbody>
        <tr v-for="item in cart.carts" :key="item.id" >
          <td class="align-middle">
              {{ item.product.title }}
            <div class="text-success" v-if="item.coupon">
              
              </div>
          </td>
          <td class="align-middle">{{ item.qty }}</td>
          <td class="align-middle">{{ item.product.price | currency }} </td>
          <td class="align-middle">{{ item.final_total | currency }} </td>
        </tr>
      </tbody>
      <tfoot>
         <tr>
         <td></td>
         <td></td>
         <td></td>
         <td> {{ cart.final_total | currency }} </td>
        </tr>
      </tfoot>
    </table>
    </div>
    </div>


  
  <div class="my-5 row justify-content-center">
  <form class="col-md-6" @submit.prevent="createOrder">
    <div class="form-group">
      <label for="useremail">Email</label>
      <input type="email" class="form-control" name="email" id="useremail"
        v-model="form.user.email" placeholder=" Email" 
        v-validate="'required|email'" :class="{'is-invalid':errors.has('email')}">
      <span class="text-danger" v-if="errors.has('email')">
      {{ errors.first('email') }}</span>
    </div>
  
    <div class="form-group">
      <label for="username"></label>
      <input type="text" class="form-control" name="name" id="username"
        v-model="form.user.name" placeholder=""
        v-validate="'required'" :class="{'is-invalid':errors.has('name')}">
      <span class="text-danger" v-if="errors.has('name')"></span>
    </div>
  
    <div class="form-group">
      <label for="usertel"></label>
      <input type="tel" class="form-control" id="usertel" name="usertel"
        v-validate="'required'" :class="{'is-invalid':errors.has('usertel')}"
        v-model="form.user.tel" placeholder="">
      <span class="text-danger" v-if="errors.has('usertel')"></span>
    </div>
  
    <div class="form-group">
      <label for="useraddress"></label>
      <input type="text" class="form-control" name="address" id="useraddress" v-model="form.user.address"
        placeholder="" v-validate="'required'" :class="{'is-invalid':errors.has('address')}" >
      <span class="text-danger" v-if="errors.has('address')"></span>
    </div>
  
    <div class="form-group">
      <label for="comment"></label>
      <textarea name="" id="comment" class="form-control" cols="30" rows="10" v-model="form.message"></textarea>
    </div>
    <div class="text-right">
      <button class="btn btn-danger"></button>
    </div>
  </form>
</div>
   
   
</div>
</template>

<script>
import $ from 'jquery';

export default {
    data(){
        return{
            products:[],
            isLoading:false,
            status:{
              loadingItem:'',
            },
            cart:{},
            form:{
              user:{
                name:'',
                email:'',
                tel:'',
                address:'',
              },
              message:'',
            },
        };
    },
    methods: {
      getProducts(){
       const url = `${process.env.APIPATH}api/${process.env.CUSTOMPATH}/products/all`;
       const vm =  this;
       vm.isLoading = true;
       this.$http.get(url).then((response) => {
        vm.products = response.data.products;
       console.log(response.data);
       vm.isLoading = false;
    });
    },
    getCart(){
      const url = `${process.env.APIPATH}api/${process.env.CUSTOMPATH}/cart`;
      const vm =  this;
      vm.isLoading = true;
      this.$http.get(url).then((response) => {
      vm.cart = response.data.data;
      console.log(response.data);
      vm.isLoading = false;
    });
    },
    createOrder(){
      const url = `${process.env.APIPATH}api/${process.env.CUSTOMPATH}/order`;
      const vm =  this;
      const order = vm.form;
      this.$validator.validate().then((result) => {
        if (result) {
          // do stuff if not valid.
          this.$http.post(url,{data:order}).then((response) => {
          console.log('',response);
          if(response.data.success){
            vm.$router.push(`/order_done/${response.data.orderId}`);
          }
          vm.isLoading = false;
      });
      }else{
        console.log('');
      }
    });
    },
    },
    created() {
      this.getProducts();
      this.getCart();
    },
}
</script>


